body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  width: 200px;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #060b35;
  padding: 32px 16px 16px;

  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    display: block;
    margin-inline: 3px;
    width: 160px;
    height: 134px;
    object-fit: contain;
  }

  &__title {
    font-weight: 500;
    line-height: 18px;
    font-size: 12px;

    margin-top: 40px;
    text-align: left;
  }

  &__subtitle {
    line-height: 14px;
    color: #616070;
    font-size: 10px;
    font-weight: 400;
    margin-top: 4px;
  }

  &__reviews {
    line-height: 14px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    margin-top: 16px;
  }

  &__reviews-stars {
    display: flex;
  }

  &__reviews-num {
    color: #060b35;
    font-size: 10px;
  }

  &__star {
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-image: url(../images/star.svg);

    margin-right: 4px;
    background-position: center;
  }

  &__star.card__star:nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
  }

  &__item-price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;

    color: #616070;
    line-height: 18px;
  }

  &__item-price-num {
    color: #060b35;
    font-size: 16px;
    font-weight: 700;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    background-color: #00acdc;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Roboto, serif;
    border: 1px solid #00acdc;
    border-radius: 5px;

    margin-top: 16px;
  }

  &__button:hover {
    background-color: #fff;
    color: #00acdc;
  }
}
